import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'
import About from './views/about.vue'
import Products from './views/products.vue'
import Service from './views/service.vue'
import Jobs from './views/jobs.vue'
import Job from './views/job.vue'
Vue.use(Router)

export default new Router({

  routes: [{
      path: '/',
      redirect: '/index',
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/products:id',
      name: 'products',
      component: Products
    },
    {
      path: '/service',
      name: 'service',
      component: Service
    },
    {
      path: '/jobs',
      name: 'jobs',
      component: Jobs
    },
    {
      path: '/job:id',
      name: 'job',
      component: Job
    }
  ]
})
