// 
<template>
  <el-main style="padding:0">
    <!--  -->
    <el-carousel
      style="background-color:#f1f3ee"
      :interval="4000"
      trigger="click"
      :height="bannerH + 'px'"
    >
      <el-carousel-item v-for="item in $t('banner')" :key="item.id">
        <el-image
          ref="image"
          style="width:100%; height:100%"
          :src="getBannerUrl(item.id)"
          fit="fill"
        ></el-image>
        <!-- <img ref="image1" style="width:100%" :src="getBannerUrl(item.id)" /> -->
      </el-carousel-item>
    </el-carousel>

    <!--  -->
    <el-divider content-position="center">
      <span class="text-placeholder">{{ $t("menu.service") }}</span>
    </el-divider>

    <div class="container">
      <!--  -->
      <el-row :gutter="20">
        <el-col :xs="24" :sm="12" v-for="item in $t('service')" :key="item.id">
          <div class="item-box">
            <div class="service-logo">
              <el-image
                :src="getServiceImg(item.id)"
                fit="scale-down"
                style="width:120px;height:120px;padding:40px 0;"
              ></el-image>
            </div>
            <div class="small-title" style="margin-top:20px">
              {{ item.contentHead }}
            </div>
            <div>
              <p class="text">{{ item.contentMain }}</p>
            </div>
          </div>
        </el-col>
      </el-row>
      <!--  -->
      <el-divider content-position="center">
        <span class="text-placeholder">{{ $t("menu.partners") }}</span>
      </el-divider>
      <!--  -->
      <el-row :type="mode">
        <el-col v-for="item in $t('partners')" :key="item.id">
          <div class="item-box-partners" style="text-align:center">
            <div>
              <el-link :href="item.href">
                <el-image
                  style="width:100%;height:100%"
                  :src="getImgUrl(item.id)"
                  fit="scale-down"
                ></el-image>
              </el-link>
            </div>
            <div class="small-title hidden-sm-and-up" style="margin-top:20px;">
              {{ item.name }}
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-divider></el-divider>
  </el-main>
</template>
<script>
export default {
  data() {
    return {
      bannerH: "",
      mode: "flex"
    };
  },

  methods: {
    //
    getServiceImg(id) {
      return require("@/assets/img/services/" +
        this.$t(`service[${id - 1}].iconUrl`));
    },
    getImgUrl(id) {
      return require("@/assets/img/partners/" +
        this.$t(`partners[${id - 1}].imgurl`));
    },
    getBannerUrl(id) {
      return require("@/assets/img/banner/" +
        this.$t(`banner[${id - 1}].imgurl`));
    },
    // 
    changemode() {
      if (document.body.clientWidth > 768) {
        this.mode = "flex";
      } else {
        this.mode = "";
      }
    },
    setsize() {
      this.bannerH = document.body.clientWidth / 3;
    }
  },
  mounted() {
    this.changemode();
    this.setsize();
    //
    window.addEventListener(
      "resize",
      () => {
        this.changemode();
        this.setsize();
      },
      false
    );
  }
};
</script>
<style>
.item-box {
  margin: 20px;
}
.item-box-partners {
  margin: 0;
}
@media (max-width: 576px) {
  .item-box-partners {
    margin: 20px;
  }
}
.service-logo {
  text-align: center;
  padding: 20px;
  border-radius: 5px;
  background: linear-gradient(25deg, rgb(219, 223, 210), white);
  box-shadow: -3px 3px 14px gray, inset 0 0 15px white;
}
</style>
