// <template>
  <el-main style="padding:20px 0">
    <div class="container">
      <div class="jobs">
        <div class="search">
          <el-row :gutter="10">
            <el-col :xs="24" :sm="20">
              <el-input
                v-model="search_content"
                placeholder=""
                clearable
              ></el-input>
            </el-col>
            <el-col :xs="24" :sm="4">
              <el-button
                type="primary"
                @keydown="search()"
                @click="search()"
                icon="el-icon-search"
                >{{ $t("com.search") }}</el-button
              >
            </el-col>
          </el-row>
        </div>
        <el-divider></el-divider>
        <div class="jobs-item">
          <el-card
            shadow="hover"
            v-for="item in search_result"
            :key="item.id"
            style="margin:20px 10px"
          >
            <el-row>
              <el-col :xs="24" :sm="10" class="jobs-head">
                <a @click="tojob(item.id)">
                  <h1 style="margin:0 ; padding:0 ">{{ item.title }}</h1>
                </a>
              </el-col>
              <el-col :xs="24" :sm="4" class="jobs-bolits">
                <i class="el-icon-time"></i>
                <span>{{ item.type }}</span>
              </el-col>
              <el-col :xs="24" :sm="10" class="jobs-bolits">
                <i class="el-icon-position"></i>
                <span>{{ item.address }}</span>
              </el-col>
            </el-row>
          </el-card>
        </div>
      </div>
    </div>
  </el-main>
</template>
<script>
export default {
  data() {
    return {
      search_content: "",
      search_result: []
    };
  },
  created() {
    //  
    this.search_result = this.$t("jobs");
  },
  methods: {
    tojob(id) {
      this.$router.push("job" + id);
    },
    // jobs.title  search_result
    search() {
      var jobs = this.$t("jobs");
      this.search_result = [];
      for (let i = 0; i < jobs.length; i++) {
        if (jobs[i].title.includes(this.search_content)) {
          this.search_result.push(jobs[i]);
        }
      }
    }
  }
};
</script>

<style scoped>
.search {
  margin: 10px;
  padding: 30px 0;
}
.jobs-item {
  margin: 10px 0;
}
.jobs-head {
  padding: 10px;
}
.jobs-head h1 {
  font-size: 16px;
  font-weight: normal;
}
.jobs-head a {
  cursor: pointer;
  text-decoration: none;
  color: #303133;
}
.jobs-head a:hover {
  color: #409eff;
}
.jobs-bolits {
  padding: 10px;
  padding-bottom: 0px;
  font-size: 14px;
}
.jobs-bolits span {
  color: #409eff;
  padding-left: 2px;
}
</style>
