// 
<template>
  <el-main style="padding:0">
    <div class="container">
      <div class="pro">
        <div class="title">{{$t(`products[${this.$route.params.id-1}].name`)}}</div>
        <el-row :gutter="50">
          <el-col class="pro-content" :span="16">
            <div class="pro-content-title">{{$t(`products[${this.$route.params.id-1}].detail`)}}</div>
            <div class="pro-content-text">{{$t(`products[${this.$route.params.id-1}].content`)}}</div>
          </el-col>
          <el-col :span="8">
            <div @mouseenter="show_detail()" @mouseleave="show_main()" class="pro-logo">
              <transition
                mode="out-in"
                enter-active-class="fadeIn animated"
                leave-active-class="fadeOut animated"
                :duration="{enter:500,leave:300}"
              >
                <el-image v-if="flag" :src="getImgUrl()" key="main"></el-image>
                <el-image v-else :src="getImgUrl_detail()" key="detail"></el-image>
              </transition>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>

  </el-main>
</template>
<i18n>


</i18n>
<script>
export default {
  data() {
    return {
      flag: true,
      test: ""
    };
  },
  methods: {
    // 
    getImgUrl() {
      return require("@/assets/img/products/" +
        this.$t(`products[${this.$route.params.id - 1}].imgurl`));
    },
    getImgUrl_detail() {
      return require("@/assets/img/products/" +
        this.$t(`products[${this.$route.params.id - 1}].imgurl_hover`));
    },
    show_detail() {
      this.flag = false;
    },
    show_main() {
      this.flag = true;
    }
  }
};
</script>
<style scoped>
.container{
  padding-top: 30px;
}
.pro {
  margin: 20px;
}
.pro-title h1 {
  color: #3f3b3a;
  font-size: 36px;
  font-weight: 500;
}
.pro-content {
  padding-top: 30px;
  padding-bottom: 50px;
}
.pro-content-title {
  margin-bottom: 20px;
  font-size: 20px;
  color: #3f3b3a;
}
.pro-content-text {
  text-indent: 2em;
  color: #747474;
  font-size: 16px;
}
.pro-logo{
  max-width: 200px;
  max-height: 200px;
}
/* animate */

@-webkit-keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

.animated {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
</style>
