<template>
  <el-header style="padding:0">
    <div class="container">
      <div class="logo" @click="$router.push('index')" style="float:left">
        <img src="@/assets/img/nullpoint.png" />
      </div>
      <!--  576px   -->
      <el-menu
        :default-active="this.$store.state.activeIndex"
        class="el-menu-demo hidden-xs-only"
        mode="horizontal"
        @select="handleSelect"
        background-color="#303133"
        text-color="#fff"
        active-text-color="#ffd04b"
        style="float:right"
      >
        <el-menu-item index="/index">{{$t("menu.home")}}</el-menu-item>
        <el-submenu index="/products">
          <template slot="title">{{$t("menu.products")}}</template>
          <el-menu-item
            v-for="item in $t('products')"
            :key="item.id"
            :index="'/products'+item.id"
          >{{item.name}}</el-menu-item>
        </el-submenu>
        <el-menu-item index="/service">{{$t("menu.service")}}</el-menu-item>
        <el-menu-item index @click="toLang">{{$t("lang")}}</el-menu-item>
      </el-menu>

      <!--  576px   -->
      <div
        class="icon hidden-sm-and-up"
        @click="isshow=!isshow"
        style="float:right; padding-right:15px;padding-top:5px;"
      >
        <i :class="isshow==false?'el-icon-s-unfold':'el-icon-s-fold'"></i>
      </div>
      <div class="clear"></div>
    </div>
    <!--  576px   -->
    <transition name="el-zoom-in-top">
      <el-menu
        style="z-index:100;"
        v-show="isshow"
        :default-active="this.$store.state.activeIndex"
        class="el-menu-vertical-demo hidden-sm-and-up"
        @select="handleSelect"
        background-color="#303133" 
        text-color="#fff"
        active-text-color="#ffd04b"
      >
        <el-menu-item index="/index">{{$t("menu.home")}}</el-menu-item>
        <el-submenu index="/products">
          <template slot="title">{{$t("menu.products")}}</template>
          <el-menu-item
            v-for="item in $t('products')"
            :key="item.id"
            :index="'/products'+item.id"
          >{{item.name}}</el-menu-item>
        </el-submenu>
        <el-menu-item index="/service">{{$t("menu.service")}}</el-menu-item>
        <el-menu-item index @click="toLang">{{$t("lang")}}</el-menu-item>
      </el-menu>
    </transition>
  </el-header>
</template>
<script>
export default {
  data() {
    return {
      isshow: false
    };
  },
  mounted() {
    // 
    this.$store.commit("change", this.$route.path);
  },
  methods: {
    //toLang() 
    toLang() {
      this.$i18n.locale =(this.$i18n.locale==="zh-CN")? "en-US" : "zh-CN";
    },
    //keyindex
    handleSelect(key, keyPath) {
      // 
      this.isshow = false;
      //
      this.$router.push(key);
      //
      this.$store.commit("change", key);
    }
  }
};
</script>
<style scpoed>
.clear {
  clear: both;
}
.el-header {
  background-color: #303133;
}
.logo {
  position: relative;
  height: 60px;
  width: 135px;
  padding-left:10px;
  vertical-align: center;
}
.logo img {
  cursor: pointer;
  margin: auto;
  width:100%;
  height:100%;
}
.icon {
  font-size: xx-large;
  cursor: pointer;
  float: right;
}
</style>
